.tabela3 table{
  width: 90%;
  float: left;
  border-spacing: 0;
}

.tabela3 table .primeiro-canto{
  border-radius: 15px 0 0 0;
  background-color: #6c0277;
}

.tabela3 table .segundo-canto{
  border-radius: 0 15px 0 0;
  background-color: #6c0277;
}

.tabela3 table .terceiro-canto{
  border-radius: 0 0 15px 0;
}

.tabela3 table .quarto-canto{
  border-radius: 0 0 0 15px;
}

.tabela3 th, .tabela3 td{
  border: 1px solid black;
  margin: 0;
}


.tabela3 table input{
  width: 95%;
  height: 95%;
  background-color: #252525;
  border: none;
}

.feito{
  background-image: url('../../images/check.png');
  width: 32px;
  height: 32px;
  float: right;
  cursor: pointer;
  width: 50%;
  background-repeat: no-repeat;
}
  

